.location-article {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}
.subheader {
  color: var(--Dark-grey-blue);
  font-size: var(--collections-header-font-size);
  font-family: var(--btn-font-family);

  display: inline-flex;
  width: fit-content;
}

.detail {
  font-size: var(--why-choose-us-paragraph-font-size);
  font-weight: var(--why-choose-us-paragraph-font-weight);
  line-height: var(--why-choose-us-paragraph-line-height);
  color: var(--grey);
  max-inline-size: 66ch;
  opacity: 0.8;
  font-family: var(--why-choose-us-font-family);
  text-align: center;
}
@media (min-width: 640px) {
  .location-article {
    align-items: flex-start;
  }
  .detail {
    text-align: left;
  }
  .subheader {
    font-size: var(--collections-header-font-size-tablet);
    height: 4.4rem;
  }
}
