.icon {
  height: 3rem;
  /* object-fit: contain; */
}
.article {
  background-color: var(--dark-cyan);

  display: flex;
  justify-content: space-around;
  align-items: center;

  height: 15rem;
  margin: 4% 0;
  flex-direction: column;
}
.article-contents {
  display: flex;
  gap: 1rem;
  align-items: center;
  flex-direction: column;
}
.article-text {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.description {
  font-size: var(--why-choose-us-paragraph-font-size);
  font-weight: var(--why-choose-us-paragraph-font-weight);
  line-height: var(--why-choose-us-paragraph-line-height);
  color: var(--light-cream);
  opacity: 0.8;
  text-align: center;
  width: 65%;
  font-family: var(--why-choose-us-font-family);
}
.subheader {
  font-weight: var(--create-plan-header-font-weight);
  font-size: 1rem;
  line-height: var(--create-plan-header-line-height);
  color: var(--light-cream);
  font-family: "Fraunces", serif;
}
@media (min-width: 640px) {
  .article-contents {
    flex-direction: row;
    padding: 0 10%;
    gap: 2rem;
  }
  .article-text {
    gap: 1rem;
    text-align: left;
  }
  .description {
    text-align: left;
    width: 100%;
  }
  .subheader {
    width: 100%;
  }
}
@media (min-width: 1440px) {
  .article {
    height: 25rem;
    border-radius: 8px;
  }
  .article-contents {
    flex-direction: column;
    padding: 6%;
  }
  .article-text,
  .description {
    text-align: center;
  }
}
